html{
    scroll-behavior: smooth;
    scrollbar-width: 10px;
}
body{
    background-color: black;
    font-family:'Courier New', Courier, monospace;
    margin: 0px;
    padding: 0;
}
.background{
    position: absolute;
    width: 100%;
    height: auto;
}
.gradient{
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle,rgba(34,193,100,0.8),rgba(0,128,0,0.3),black);
    background-size: 200%;
    filter: blur(200px);
    z-index: 0;
    animation: moveGrad 3s infinite alternate ease-in-out;
}
@keyframes moveGrad {
    0%{
        background-position: 0%;
        transform: translate(-10%,-25%);
    }
    100%{
        background-position: 10%;
        transform: translate(-40%,-60%);
    }
    
}
.matrix-container{
    color: #080;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    height: auto;
    width: 100%;
    box-shadow: inset 0 -20px 100px black,inset  0 -30px 100px black;
}
.matrix-row{
    display: flex;
    z-index: -1;
}
.matrix-cell{
    font-size: 16px;
    opacity: 0.5;
}

.matrix-cell.active{
    opacity: 1;
    color: #0f0;
    text-shadow: 0 0 5px green,0 0 10px green;
}
.content{
    position: absolute;
    height: 100vh;
    width: 100%;
    background-color: rgba(5, 5, 5, 0.4);
    backdrop-filter: blur(0.5px);
    top: 0%;
    z-index: 2;
    justify-content: center;
    align-items: center;
}
.heading{
    position: relative;
    padding: 20px;
    color:#0f0;
    text-align: center;
    font-size: larger;
    text-shadow: #0a0 0 0 5px, #0a0 0 0 20px;
}
.typer{
    display: flex;
    justify-content: center;
    align-items: center;
}
.subheading{
    color: #0c0;
    padding: 40px;
    text-align: center;
    position: relative;
    font-size: 20px;
    text-shadow: #0c0 0 0px 3px;
    display: flex;
    border-radius: 2px solid #f00;
    white-space:nowrap;
    overflow: hidden;
    flex-wrap: wrap;
    text-wrap: stable;
    /* animation: typing 5s steps(25) forwards; */
}
