.aboutMe{
    color: #0f0;
    padding: 30px;
    text-align: center;
    position: relative;
    font-size: 26px;
    font-weight: bold;
}
.intro{
    position: relative;
    text-align: center;
    color: #080;
    width: 40%;
    left: 30%;
    font-size: 23px;
}
.h2{
    color: #0f0;
    font-size: 26px;
    position: relative;
    text-align: center;
    padding: 20px;
}
.projects{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.project{
    padding: 20px;
    background:linear-gradient(0deg, #010,transparent);
    margin-top: 30px;
    width: 300px;
    border-radius: 10px ;
    box-shadow: #0f0 0 0 10px;
    color: #060;
    text-wrap: stable;
}
img{
    height:40px;
    width: 40px;
    border-radius: 20px;
}
h3{
    color:#0c0;
}
a{
    color: #0c0;
    text-decoration: underline;
    text-decoration-color: #0a0;
}
.contacts{
    display: flex;
    justify-content:center;
    flex-wrap: wrap;
}
.contact{
    margin-left: 40px;
    margin-right: 40px;
}
hr{
    width: 90%;
    color: #0f0;
    background-color: #0c0;
    height: 3px;
    box-shadow: 0 0 10px #0f0;
    margin-top: 20px;
    margin-bottom: 40px;
}